}
html {
  scroll-behavior: smooth;
}
:root {
  --primary: #396dff;
  --primary--dim: #6987db;
  --darkgrey: #d4d4d4;
  --medgrey: #e0e0e0;
  --grey: #f7f7f7;
  --white: #fff;

  --silver-pink: #d0b8acff;
  --unbleached-silk: #f3d8c7ff;
  --pale-pink: #f1d1d1ff;
  --linen: #efe5dcff;
  --baby-powder: #fbfefbff;

  --background: rgba(243, 216, 199, 0.2);
  --xxs:8px;
  --xs: 20px;
  --s: 24px;
  --m: 36px;
  --l: 72px;
  --xl: 144px;
  --xxl: 288px;
}
@media only screen and (max-width: 1370px) {
  :root {
    --xxs:8px;
    --xs: 16px;
    --s: 20px;
    --m: 24px;
   --l: 36px;
    --xl: 72px;
    --xxl: 144px;
  }
}
@media only screen and (max-width: 375px) {
  :root {
    --xxs:8px;
    --xs: 12px;
    --s: 20px;
    --m: 24px;
   --l: 36px;
    --xl: 36px;
    --xxl: 36px;
  }
}
.link {
  color: #d0b8acff;
  color: var(--silver-pink);
  font-size: 24px;
  font-size: var(--s);
  transition: all .2s ease-in-out;
}
.link:hover {
  color: #f3d8c7ff;
  color: var(--unbleached-silk);
}
.space {
  height: 70px;
}
.title {
  font-family: 'Playfair Display', serif;
  color: #efe5dcff;
  color: var(--linen);
  font-size: 144px;
  font-size: var(--xl);
}
.ProjectModal-title {
  font-family: 'Playfair Display', serif;
  width: 100%;
  color: #efe5dcff;
  color: var(--linen);
  font-size: 72px;
  font-size: var(--l);
  text-align: center;
  margin: auto;
}
.ProjectModal-body {
  padding: 24px 0px;
  padding: var(--s) 0px;
}
.ProjectModal-image > img {
  width: 100% !important;
}
.ProjectModal-image {
  display: flex;
  flex-direction: column;
  padding-left: 24px;
  padding-left: var(--s);
}
.ProjectModal-grid {
  padding: 24px;
  padding: var(--s);
}

h4 {
  font-family: 'Lato', sans-serif;
  color: #d0b8acff;
  color: var(--silver-pink);
  font-size: 24px;
  font-size: var(--s);
  font-style: italic;
}

p {
